import { setTimeout } from "timers";
import userInfoModal from "@/components/userInfoModal";
export default {
  name: "curriculumLiveBroadcast",
  components: {
    userInfoModal
  },
  data() {
    return {
      swiperOption: {
        autoplay: false, //自动轮播间隔
        speed: 1000, // 轮播速度
        // loop: true, //开启轮播图前后循环模式
        // 设置轮播点,并且允许点击切换
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      option: {
        // 是否全屏显示轮播
        isFullScreen: false,
        // 轮播是否已经启动
        isCarouselType: false,
        // 蒙层
        IsMongolia: false,
        // 是否发送消息
        IsSendMsg: false,
        // 礼物模态框
        Isgift: false,
        timer: null,
        intTime: null,
        heightStyle: ""
      },
      datas: {
        Listdata4: [
          { userName: "张三", msg: "我是一只小小鸟1" },
          { userName: "张三", msg: "我是一只小小鸟2" },
          { userName: "张三", msg: "我是一只小小鸟3" },
          { userName: "张三", msg: "我是一只小小鸟4" },
          { userName: "张三", msg: "我是一只小小鸟5" },
          { userName: "张三", msg: "我是一只小小鸟6" },
          { userName: "张三", msg: "我是一只小小鸟7" },
          { userName: "张三", msg: "我是一只小小鸟8" },
          { userName: "张三", msg: "我是一只小小鸟9" },
          { userName: "张三", msg: "我是一只小小鸟10" },
          { userName: "张三", msg: "我是一只小小鸟4" },
          { userName: "张三", msg: "我是一只小小鸟5" },
          { userName: "张三", msg: "我是一只小小鸟6" },
          { userName: "张三", msg: "我是一只小小鸟7" }
          // { userName: "张三", msg: "我是一只小小鸟8" },
          // { userName: "张三", msg: "我是一只小小鸟9" }
        ]
      }
    };
  },
  //   直接执行
  mounted() {
    this.setBodyEle();
    this.isOpenCarousel();
  },
  //   方法
  methods: {
    // 公用的选择方法
    selectRankingAll: function(item, index, ListName) {
      const _this = this;
      _this.option[ListName].map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.option[ListName], idx, event);
        } else {
          event.type = false;
          _this.$set(_this.option[ListName], idx, event);
        }
      });
    },
    setBodyEle: function() {
      let _body = document.querySelector("body");
      let _html = document.querySelector("html");
      let _content = document.getElementById("app");
      let _main = document.getElementById("broadcast_main");
      let _top = document.getElementById("broadcast_top");
      _html.style.height = 100 + "%";
      _body.style.height = 100 + "%";
      _content.style.height = 100 + "%";
      _main.style.paddingTop = _top.offsetHeight + "px";
    },
    // 设置聊天自动滚动
    setScrollUserInformation: function() {
      let scrollHeight = $("ul li section:first").outerHeight(true);
      let $uList = $(".scroll-box ul");

      this.option.isCarouselType = true;

      //开启动定时器
      this.option.timer = setInterval(function() {
        scrollList($uList);
      }, 3000);

      //滚动动画
      function scrollList(obj) {
        //获得当前<li>的高度

        //滚动出一个<li>的高度
        $uList.stop().animate(
          {
            marginTop: -scrollHeight
          },
          800,
          function() {
            //动画结束后，将当前<ul>marginTop置为初始值0状态，再将第一个<li>拼接到末尾。
            $uList
              .css({
                marginTop: 0
              })
              .find("li:first")
              .appendTo($uList);
          }
        );
      }
    },
    isOpenCarousel: function() {
      const _this = this;

      _this.option.intTime = setInterval(function() {
        // console.log("1234");
        // 首先判断数组的长度大于4,并且轮播没有被启动的情况下激活轮播功能
        if (_this.datas.Listdata4.length > 4) {
          //
          if (!_this.option.isCarouselType) {
            _this.setScrollUserInformation();
          } else {
          }
        } else {
        }
      }, 5000);
    },
    openFullScreen: function(list) {
      // console.log(list);
      if (this.option.isFullScreen) {
        this.option.isFullScreen = false;
        this.setStyleHeight(list.length);
      } else {
        if (list.length > 4 && list.length < 14) {
          // 开启全屏模式
          this.option.isFullScreen = true;
          this.setStyleHeight(list.length);

          if (this.option.isCarouselType) {
            // console.log(this.option.intTime);
            // console.log(this.option.timer);
            clearInterval(this.option.timer);
            this.option.isCarouselType = false;
          } else {
          }
        } else {
          if (list.length <= 4) {
            return;
          }
          this.option.isFullScreen = true;
          this.setStyleHeight(list.length);
        }
      }
    },
    setStyleHeight: function(length) {
      let scrollHeight = $("ul li section:first").outerHeight(true);
      if (this.option.isFullScreen) {
        if (length < 14) {
          setTimeout(function() {
            $(".FullScreen").height(length * scrollHeight + length * 1.8);
          }, 0);
        } else {
          setTimeout(function() {
            $(".FullScreen").height(13 * scrollHeight + 13 * 1.8);
          }, 0);
        }
      } else {
        if (length >= 4) {
          console.log(length);
          setTimeout(function() {
            $(".noFullScreen").height(4 * scrollHeight + 4 * 1.8);
          }, 0);
        } else {
          setTimeout(function() {
            $(".noFullScreen").height(length * scrollHeight + length * 1.8);
          }, 0);
        }
      }
    },
    remCustomModal: function(name) {
      console.log(name)
      if (name == "IsUserModal") {
        this.option.IsMongolia = false;
      }
      if (name == "Isgift") {
        this.option.Isgift = false;
      }
      this.setBodyEleModal();
    },
    setBodyEleModal: function() {
      let _body = document.querySelector("body");
      let _html = document.querySelector("html");
      if (this.option.IsMongolia || this.option.Isgift) {
        _body.style.overflow = "hidden";
        _html.style.overflow = "hidden";
        _html.style.height = 100 + "%";
        _body.style.height = 100 + "%";
      } else {
        _body.style.overflow = "auto";
        _html.style.overflow = "auto";
        _html.style.height = "auto";
        _body.style.height = "auto";
      }
    },
    openUserInfo: function() {
      this.option.IsMongolia = true;
      this.setBodyEleModal();
    },
    focusfns: function() {
      // 获取焦点
      this.option.IsSendMsg = true;
    },
    blurfns: function() {
      this.option.IsSendMsg = false;
    },
    openGive: function() {
      this.option.Isgift = true;
      this.setBodyEleModal();
    }
  }
};



